  .contact-section {
      background-image: url('../assets/binary_tree.jpg');
      background-attachment: fixed;
      background-size: cover;
      padding: 60px 0 40px 0;
      text-align: center;
      transition: height 1s ease-out;
  }

  .inner-input-container {
      text-align: left;
      margin-top: 30px;
  }

  .inner-input-container>span {
      position: relative;
      left: 6px;
      color: #e0e0e0;
      font-size: 20px;
  }

  .inner-width {
      max-width: 600px;
      margin: auto;
      padding: 0 20px;
  }

  .contact-section h1 {
      font-size: 30px;
      color: #FFF;
      text-transform: uppercase;
      letter-spacing: 4px;
      font-weight: 400;
  }


  .name {
      background: none;
      border: none;
      outline: none;
      border-bottom: 1px solid;
      color: #FFF;
      padding: 10px 6px;
      font-size: 24px;
      margin-bottom: 40px;
  }

  .name {
      float: center;
      width: 540px;
  }

  .buttons {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
  }

  .buttons>* {
      margin-right: 15px;
  }

  .contact-section button {
      margin-bottom: 20px;
      outline: none;
      background: none;
      color: #fafafa;
      border: 1px solid #e0e0e0;
      padding: 12px 40px;
      border-radius: 8px;
      text-transform: uppercase;
      font-size: 14px;
      transition: 0.4s linear;
      cursor: pointer;
  }

  .contact-section button:hover {
      background: #fafafa;
      color: #212121;
  }


  @media screen and (max-width:600px) {

      .name {
          width: 100%;
      }
  }

